#app-main {
  background-color: #fefefe;
}

.logo {
  text-align: left;
  font-size: 2vw;
  font-weight: bolder;
  padding: 1vw;
  padding-left: 1vw;
  border: 0;
}

.toolContainer {
  background-color: rgb(255, 255, 255);
}

.icon {
  height: 2vw;
  width: 2vw;
  padding-right: 1vw;
}

.icon#home {
  padding-right: 1vb;
}

.plantImage {
  display: flex;
  align-items: center;
  width: 100px;
  height: 100px;
}

.plantImage:hover {
  cursor: pointer;
  transition: 300ms ease;
  width: 120px;
  height: 120px;
}

/* App.css */

/* Modal styles */
.modal {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  background-color: #fefefe;
  padding: 20px;
  border-radius: 5px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.toolbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

button {
  font-size: 2vw;
  padding: 1vw;
  background-color: rgb(255, 255, 255);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #217b17;
}

.intro-section {
  text-align: center;
  padding: 20px;
}

.image-section {
  position: relative;
  text-align: center;
  margin-top: 20px;
}

.frontimage {
  width: 100%;
  height: 300px;
  max-width: 1;
  object-fit: cover;
  padding-top: 0;
}

.image-text {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: xx-large;
  background-color: #fefefe61;
  height: 60%;
  width: 100%;
}

#welcome {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#sprout-view {
  margin-left: 1vw;
}

.cta-section {
  text-align: center;
  margin-top: 20px;
}

.explore-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.explore-button:hover {
  background-color: #167222;
}

.choice {
  color: black;
  font-size:large;
}

.flex {
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.plant-section {
  padding-top: 10vw;
}